.preloader {
  width: 100%;
  height: 100%;
  z-index: 3;
  opacity: 1;
  visibility: visible;
  background: #222;
  transition: all 1s;
  position: fixed;
  top: 0;
  left: 0;
}

.preloader .loader {
  height: 100px;
  width: 100px;
  border: 4px solid #0000;
  border-top-color: #8b00ff;
  border-radius: 50%;
  margin: -30px;
  animation: spin 2s linear infinite;
  position: absolute;
  top: 50%;
  left: 50%;
}

.preloader .loader:before, .preloader .loader:after {
  content: "";
  border: 4px solid #0000;
  border-radius: 50%;
  position: absolute;
}

.preloader .loader:before {
  border-top-color: #086cf9;
  animation: spin 3s linear infinite;
  inset: -12px;
}

.preloader .loader:after {
  border-top-color: #fff;
  animation: spin 4s linear infinite;
  inset: 6px;
}

.preloader.done {
  opacity: 0;
  visibility: hidden;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

